// Platform Stats
const platformStats: PlatformStats[] = [
  { platform: 'Google', icon: 'G', color: 'from-blue-500 to-blue-600', totalReviews: 127, averageRating: 4.8, connected: true, lastSync: '2 hours ago' },
  { platform: 'Facebook', icon: 'f', color: 'from-blue-600 to-blue-700', totalReviews: 89, averageRating: 4.6, connected: true, lastSync: '3 hours ago' },
  { platform: 'Yelp', icon: 'Y', color: 'from-red-500 to-red-600', totalReviews: 45, averageRating: 4.2, connected: true, lastSync: '1 day ago' },
  { platform: 'Trustpilot', icon: 'T', color: 'from-green-500 to-green-600', totalReviews: 63, averageRating: 4.7, connected: false },
  { platform: 'TripAdvisor', icon: 'TA', color: 'from-emerald-500 to-teal-600', totalReviews: 0, averageRating: 0, connected: false },
]

export default function ReputationPage() {
  const pathname = usePathname()
  const [reviews, setReviews] = useState<Review[]>(mockReviews)
  const [reviewRequests, setReviewRequests] = useState<ReviewRequest[]>(mockReviewRequests)
  const [loading, setLoading] = useState(false)
  const [platformFilter, setPlatformFilter] = useState('')
  const [ratingFilter, setRatingFilter] = useState('')
  const [sentimentFilter, setSentimentFilter] = useState('')
  const [replyFilter, setReplyFilter] = useState('')
  const [showReplyModal, setShowReplyModal] = useState(false)
  const [selectedReview, setSelectedReview] = useState<Review | null>(null)
  const [replyText, setReplyText] = useState('')
  const [showRequestModal, setShowRequestModal] = useState(false)
  const [requestEmail, setRequestEmail] = useState('')
  const [requestName, setRequestName] = useState('')
  const [requestPhone, setRequestPhone] = useState('')

  // Calculate overall stats
  const totalReviews = platformStats.reduce((sum, p) => sum + p.totalReviews, 0)
  const overallRating = platformStats.filter(p => p.totalReviews > 0).reduce((sum, p) => sum + p.averageRating * p.totalReviews, 0) / totalReviews || 0
  const positiveReviews = reviews.filter(r => r.sentiment === 'positive').length
  const negativeReviews = reviews.filter(r => r.sentiment === 'negative').length
  const unrepliedReviews = reviews.filter(r => !r.replied).length

  // Filter reviews
  const filteredReviews = reviews.filter(review => {
    if (platformFilter && review.platform !== platformFilter) return false
    if (ratingFilter && review.rating !== parseInt(ratingFilter)) return false
    if (sentimentFilter && review.sentiment !== sentimentFilter) return false
    if (replyFilter === 'replied' && !review.replied) return false
    if (replyFilter === 'unreplied' && review.replied) return false
    return true
  })

  // Get platform icon/color
  const getPlatformStyle = (platform: string) => {
    switch (platform) {
      case 'google':
        return { icon: 'G', bg: 'bg-white', text: 'text-blue-500', border: 'border-blue-200' }
      case 'facebook':
        return { icon: 'f', bg: 'bg-blue-600', text: 'text-white', border: 'border-blue-600' }
      case 'yelp':
        return { icon: 'Y', bg: 'bg-red-500', text: 'text-white', border: 'border-red-500' }
      case 'trustpilot':
        return { icon: 'T', bg: 'bg-green-500', text: 'text-white', border: 'border-green-500' }
      case 'tripadvisor':
        return { icon: 'TA', bg: 'bg-emerald-500', text: 'text-white', border: 'border-emerald-500' }
      default:
        return { icon: '?', bg: 'bg-gray-500', text: 'text-white', border: 'border-gray-500' }
    }
  }

  // Get sentiment style
  const getSentimentStyle = (sentiment: string) => {
    switch (sentiment) {
      case 'positive':
        return 'bg-emerald-100 text-emerald-700'
      case 'neutral':
        return 'bg-amber-100 text-amber-700'
      case 'negative':
        return 'bg-red-100 text-red-700'
      default:
        return 'bg-gray-100 text-gray-700'
    }
  }

  // Get request status style
  const getRequestStatusStyle = (status: string) => {
    switch (status) {
      case 'completed':
        return 'bg-emerald-100 text-emerald-700'
      case 'opened':
        return 'bg-blue-100 text-blue-700'
      case 'sent':
        return 'bg-amber-100 text-amber-700'
      case 'pending':
        return 'bg-gray-100 text-gray-700'
      case 'declined':
        return 'bg-red-100 text-red-700'
      default:
        return 'bg-gray-100 text-gray-700'
    }
  }

  // Handle reply submission
  const handleReplySubmit = () => {
    if (!selectedReview || !replyText.trim()) return

    setReviews(prev => prev.map(r => {
      if (r.id === selectedReview.id) {
        return {
          ...r,
          replied: true,
          replyContent: replyText,
          replyDate: new Date().toISOString().split('T')[0],
        }
      }
      return r
    }))

    setShowReplyModal(false)
    setSelectedReview(null)
    setReplyText('')
  }

  // Handle review request
  const handleSendRequest = () => {
    if (!requestEmail.trim() || !requestName.trim()) return

    const newRequest: ReviewRequest = {
      id: `req-${Date.now()}`,
      contactName: requestName,
      contactEmail: requestEmail,
      contactPhone: requestPhone || undefined,
      status: 'sent',
      sentAt: new Date().toISOString().split('T')[0],
    }

    setReviewRequests(prev => [newRequest, ...prev])
    setShowRequestModal(false)
    setRequestEmail('')
    setRequestName('')
    setRequestPhone('')
  }

  // Render stars
  const renderStars = (rating: number, size: string = 'w-4 h-4') => {
    return (
      <div className="flex items-center gap-0.5">
        {[1, 2, 3, 4, 5].map(star => (
          <svg
            key={star}
            className={`${size} ${star <= rating ? 'text-amber-400' : 'text-gray-300'}`}
            fill="currentColor"
            viewBox="0 0 20 20"
          >
            <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
          </svg>
        ))}
      </div>
    )
  }

  return (
    <div className="space-y-6 pb-8">
      {/* Page Header with Tabs */}
      <div className="flex-none bg-white border-b border-slate-200 -mx-4 sm:-mx-6 lg:-mx-8 -mt-4 sm:-mt-6 lg:-mt-8 px-4 sm:px-6 lg:px-8 pt-4 sm:pt-6 lg:pt-8 pb-0 mb-8">
        <PageHeader
          title="Reputation Management"
          description="Monitor and manage your online reputation across all platforms"
          icon="fa-star"
          actions={
            <div className="flex items-center gap-2">
              <button
                onClick={() => setShowRequestModal(true)}
                className="px-5 py-2.5 bg-gradient-to-r from-amber-500 to-orange-500 text-white rounded-xl font-medium hover:from-amber-600 hover:to-orange-600 transition-all shadow-lg flex items-center gap-2"
              >
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 10h.01M12 10h.01M16 10h.01M9 16H5a2 2 0 01-2-2V6a2 2 0 012-2h14a2 2 0 012 2v8a2 2 0 01-2 2h-5l-5 5v-5z" />
                </svg>
                Request Review
              </button>
            </div>
          }
        />
        
        {/* Tab Navigation */}
        <nav className="flex items-center gap-1 overflow-x-auto -mb-px">
          {REPUTATION_TABS.map(tab => (
            <Link
              key={tab.id}
              href={tab.href}
              className={`px-4 py-3 text-sm font-medium border-b-2 transition-colors whitespace-nowrap ${
                tab.id === 'overview'
                  ? 'border-amber-500 text-amber-600'
                  : 'border-transparent text-slate-500 hover:text-slate-700 hover:border-slate-300'
              }`}
            >
              {tab.label}
            </Link>
          ))}
        </nav>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-2 lg:grid-cols-5 gap-4">
        {/* Overall Rating */}
        <div className="bg-gradient-to-br from-amber-500 to-orange-500 rounded-2xl shadow-lg p-6 text-white">
          <div className="flex items-center justify-between mb-2">
            <p className="text-amber-100 text-sm font-medium">Overall Rating</p>
            <div className="w-10 h-10 bg-white/20 rounded-xl flex items-center justify-center backdrop-blur-sm">
              <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 20 20">
                <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
              </svg>
            </div>
          </div>
          <p className="text-3xl font-bold">{overallRating.toFixed(1)}</p>
          <div className="flex items-center gap-1 mt-1">
            {renderStars(Math.round(overallRating), 'w-4 h-4')}
          </div>
        </div>

        {/* Total Reviews */}
        <div className="bg-white rounded-2xl shadow-sm border border-slate-200 p-6">
          <div className="flex items-center justify-between mb-2">
            <p className="text-slate-600 text-sm font-medium">Total Reviews</p>
            <div className="w-10 h-10 bg-blue-50 rounded-xl flex items-center justify-center">
              <svg className="w-5 h-5 text-blue-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 8h10M7 12h4m1 8l-4-4H5a2 2 0 01-2-2V6a2 2 0 012-2h14a2 2 0 012 2v8a2 2 0 01-2 2h-3l-4 4z" />
              </svg>
            </div>
          </div>
          <p className="text-3xl font-bold text-slate-900">{totalReviews}</p>
          <p className="text-xs text-emerald-600 mt-1">+12 this month</p>
        </div>

        {/* Positive Reviews */}
        <div className="bg-white rounded-2xl shadow-sm border border-slate-200 p-6">
          <div className="flex items-center justify-between mb-2">
            <p className="text-slate-600 text-sm font-medium">Positive</p>
            <div className="w-10 h-10 bg-emerald-50 rounded-xl flex items-center justify-center">
              <svg className="w-5 h-5 text-emerald-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M14.828 14.828a4 4 0 01-5.656 0M9 10h.01M15 10h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
            </div>
          </div>
          <p className="text-3xl font-bold text-slate-900">{positiveReviews}</p>
          <p className="text-xs text-slate-500 mt-1">{Math.round((positiveReviews / reviews.length) * 100)}% of reviews</p>
        </div>

        {/* Negative Reviews */}
        <div className="bg-white rounded-2xl shadow-sm border border-slate-200 p-6">
          <div className="flex items-center justify-between mb-2">
            <p className="text-slate-600 text-sm font-medium">Negative</p>
            <div className="w-10 h-10 bg-red-50 rounded-xl flex items-center justify-center">
              <svg className="w-5 h-5 text-red-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9.172 16.172a4 4 0 015.656 0M9 10h.01M15 10h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
            </div>
          </div>
          <p className="text-3xl font-bold text-slate-900">{negativeReviews}</p>
          <p className="text-xs text-slate-500 mt-1">{Math.round((negativeReviews / reviews.length) * 100)}% of reviews</p>
        </div>

        {/* Needs Reply */}
        <div className="bg-white rounded-2xl shadow-sm border border-slate-200 p-6">
          <div className="flex items-center justify-between mb-2">
            <p className="text-slate-600 text-sm font-medium">Needs Reply</p>
            <div className="w-10 h-10 bg-amber-50 rounded-xl flex items-center justify-center">
              <svg className="w-5 h-5 text-amber-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 10h10a8 8 0 018 8v2M3 10l6 6m-6-6l6-6" />
              </svg>
            </div>
          </div>
          <p className="text-3xl font-bold text-slate-900">{unrepliedReviews}</p>
          <p className="text-xs text-amber-600 mt-1">Respond soon!</p>
        </div>
      </div>

      {/* Platform Cards */}
      <div className="bg-white rounded-2xl shadow-sm border border-slate-200 p-6">
        <h3 className="text-lg font-semibold text-slate-900 mb-4">Connected Platforms</h3>
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-5 gap-4">
          {platformStats.map(platform => (
            <div
              key={platform.platform}
              className={`p-4 rounded-xl border-2 ${platform.connected ? 'border-slate-200' : 'border-dashed border-slate-300'}`}
            >
              <div className="flex items-center gap-3 mb-3">
                <div className={`w-10 h-10 rounded-lg bg-gradient-to-br ${platform.color} flex items-center justify-center text-white font-bold text-sm`}>
                  {platform.icon}
                </div>
                <div>
                  <p className="font-medium text-slate-900">{platform.platform}</p>
                  {platform.connected ? (
                    <p className="text-xs text-emerald-600 flex items-center gap-1">
                      <span className="w-1.5 h-1.5 bg-emerald-500 rounded-full"></span>
                      Connected
                    </p>
                  ) : (
                    <p className="text-xs text-slate-400">Not connected</p>
                  )}
                </div>
              </div>

              {platform.connected ? (
                <>
                  <div className="flex items-center justify-between text-sm">
                    <span className="text-slate-600">Rating</span>
                    <div className="flex items-center gap-1">
                      <span className="font-semibold text-slate-900">{platform.averageRating}</span>
                      <svg className="w-4 h-4 text-amber-400" fill="currentColor" viewBox="0 0 20 20">
                        <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
                      </svg>
                    </div>
                  </div>
                  <div className="flex items-center justify-between text-sm mt-1">
                    <span className="text-slate-600">Reviews</span>
                    <span className="font-semibold text-slate-900">{platform.totalReviews}</span>
                  </div>
                  <p className="text-xs text-slate-400 mt-2">Synced {platform.lastSync}</p>
                </>
              ) : (
                <button className="w-full mt-2 px-3 py-2 text-sm font-medium text-amber-600 bg-amber-50 rounded-lg hover:bg-amber-100 transition-colors">
                  Connect
                </button>
              )}
            </div>
          ))}
        </div>
      </div>

      {/* Reviews Section */}
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Recent Reviews */}
        <div className="lg:col-span-2 bg-white rounded-2xl shadow-sm border border-slate-200 overflow-hidden">
          <div className="p-6 border-b border-slate-200">
            <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-4">
              <h3 className="text-lg font-semibold text-slate-900">Recent Reviews</h3>
              <div className="flex flex-wrap gap-2">
                <select
                  value={platformFilter}
                  onChange={(e) => setPlatformFilter(e.target.value)}
                  className="px-3 py-1.5 text-sm border border-slate-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-amber-500"
                >
                  <option value="">All Platforms</option>
                  <option value="google">Google</option>
                  <option value="facebook">Facebook</option>
                  <option value="yelp">Yelp</option>
                  <option value="trustpilot">Trustpilot</option>
                </select>
                <select
                  value={ratingFilter}
                  onChange={(e) => setRatingFilter(e.target.value)}
                  className="px-3 py-1.5 text-sm border border-slate-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-amber-500"
                >
                  <option value="">All Ratings</option>
                  <option value="5">5 Stars</option>
                  <option value="4">4 Stars</option>
                  <option value="3">3 Stars</option>
                  <option value="2">2 Stars</option>
                  <option value="1">1 Star</option>
                </select>
                <select
                  value={replyFilter}
                  onChange={(e) => setReplyFilter(e.target.value)}
                  className="px-3 py-1.5 text-sm border border-slate-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-amber-500"
                >
                  <option value="">All Status</option>
                  <option value="replied">Replied</option>
                  <option value="unreplied">Needs Reply</option>
                </select>
              </div>
            </div>
          </div>

          <div className="divide-y divide-slate-200 max-h-[600px] overflow-y-auto">
            {filteredReviews.map(review => {
              const platformStyle = getPlatformStyle(review.platform)
              return (
                <div key={review.id} className="p-6 hover:bg-slate-50 transition-colors">
                  <div className="flex items-start gap-4">
                    {/* Platform Icon */}
                    <div className={`w-10 h-10 rounded-lg ${platformStyle.bg} ${platformStyle.text} border ${platformStyle.border} flex items-center justify-center font-bold text-sm flex-shrink-0`}>
                      {platformStyle.icon}
                    </div>

                    <div className="flex-1 min-w-0">
                      {/* Header */}
                      <div className="flex items-start justify-between gap-4 mb-2">
                        <div>
                          <div className="flex items-center gap-2">
                            <h4 className="font-medium text-slate-900">{review.author}</h4>
                            {review.verified && (
                              <svg className="w-4 h-4 text-blue-500" fill="currentColor" viewBox="0 0 20 20">
                                <path fillRule="evenodd" d="M6.267 3.455a3.066 3.066 0 001.745-.723 3.066 3.066 0 013.976 0 3.066 3.066 0 001.745.723 3.066 3.066 0 012.812 2.812c.051.643.304 1.254.723 1.745a3.066 3.066 0 010 3.976 3.066 3.066 0 00-.723 1.745 3.066 3.066 0 01-2.812 2.812 3.066 3.066 0 00-1.745.723 3.066 3.066 0 01-3.976 0 3.066 3.066 0 00-1.745-.723 3.066 3.066 0 01-2.812-2.812 3.066 3.066 0 00-.723-1.745 3.066 3.066 0 010-3.976 3.066 3.066 0 00.723-1.745 3.066 3.066 0 012.812-2.812zm7.44 5.252a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                              </svg>
                            )}
                          </div>
                          <div className="flex items-center gap-2 mt-1">
                            {renderStars(review.rating)}
                            <span className="text-xs text-slate-500">{review.date}</span>
                          </div>
                        </div>
                        <span className={`px-2 py-1 text-xs font-medium rounded-full ${getSentimentStyle(review.sentiment)}`}>
                          {review.sentiment}
                        </span>
                      </div>

                      {/* Content */}
                      <p className="text-slate-700 text-sm leading-relaxed">{review.content}</p>

                      {/* Reply */}
                      {review.replied && review.replyContent && (
                        <div className="mt-3 pl-4 border-l-2 border-amber-300 bg-amber-50 rounded-r-lg p-3">
                          <p className="text-xs font-medium text-amber-700 mb-1">Your Response â€¢ {review.replyDate}</p>
                          <p className="text-sm text-amber-900">{review.replyContent}</p>
                        </div>
                      )}

                      {/* Actions */}
                      <div className="flex items-center gap-3 mt-3">
                        {!review.replied && (
                          <button
                            onClick={() => {
                              setSelectedReview(review)
                              setShowReplyModal(true)
                            }}
                            className="px-3 py-1.5 text-sm font-medium text-amber-600 bg-amber-50 rounded-lg hover:bg-amber-100 transition-colors"
                          >
                            Reply
                          </button>
                        )}
                        <button className="px-3 py-1.5 text-sm font-medium text-slate-600 hover:text-slate-800 transition-colors">
                          View on {review.platform.charAt(0).toUpperCase() + review.platform.slice(1)}
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              )
            })}
          </div>
        </div>

        {/* Review Requests */}
        <div className="bg-white rounded-2xl shadow-sm border border-slate-200 overflow-hidden">
          <div className="p-6 border-b border-slate-200">
            <div className="flex items-center justify-between">
              <h3 className="text-lg font-semibold text-slate-900">Review Requests</h3>
              <button
                onClick={() => setShowRequestModal(true)}
                className="p-2 text-amber-600 hover:bg-amber-50 rounded-lg transition-colors"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
                </svg>
              </button>
            </div>
          </div>

          <div className="divide-y divide-slate-200 max-h-[500px] overflow-y-auto">
            {reviewRequests.map(request => (
              <div key={request.id} className="p-4 hover:bg-slate-50 transition-colors">
                <div className="flex items-start justify-between gap-3">
                  <div className="flex-1 min-w-0">
                    <h4 className="font-medium text-slate-900 truncate">{request.contactName}</h4>
                    <p className="text-sm text-slate-600 truncate">{request.contactEmail}</p>
                    {request.sentAt && (
                      <p className="text-xs text-slate-400 mt-1">Sent: {request.sentAt}</p>
                    )}
                  </div>
                  <div className="flex flex-col items-end gap-1">
                    <span className={`px-2 py-1 text-xs font-medium rounded-full ${getRequestStatusStyle(request.status)}`}>
                      {request.status}
                    </span>
                    {request.rating && (
                      <div className="flex items-center gap-1">
                        {renderStars(request.rating, 'w-3 h-3')}
                      </div>
                    )}
                  </div>
                </div>
              </div>
            ))}
          </div>

          <div className="p-4 bg-slate-50 border-t border-slate-200">
            <div className="grid grid-cols-2 gap-4 text-center">
              <div>
                <p className="text-2xl font-bold text-slate-900">{reviewRequests.filter(r => r.status === 'completed').length}</p>
                <p className="text-xs text-slate-600">Completed</p>
              </div>
              <div>
                <p className="text-2xl font-bold text-slate-900">
                  {Math.round((reviewRequests.filter(r => r.status === 'completed').length / reviewRequests.length) * 100)}%
                </p>
                <p className="text-xs text-slate-600">Conversion Rate</p>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Reply Modal */}
      {showReplyModal && selectedReview && (
        <div className="fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-2xl max-w-xl w-full shadow-2xl overflow-hidden">
            <div className="bg-gradient-to-r from-amber-500 to-orange-500 px-6 py-4 flex items-center justify-between">
              <div className="flex items-center gap-3">
                <div className="w-10 h-10 bg-white/20 rounded-xl flex items-center justify-center backdrop-blur-sm">
                  <svg className="w-5 h-5 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 10h10a8 8 0 018 8v2M3 10l6 6m-6-6l6-6" />
                  </svg>
                </div>
                <div>
                  <h3 className="text-lg font-semibold text-white">Reply to Review</h3>
                  <p className="text-sm text-amber-100">Respond to {selectedReview.author}</p>
                </div>
              </div>
              <button
                onClick={() => {
                  setShowReplyModal(false)
                  setSelectedReview(null)
                  setReplyText('')
                }}
                className="text-white hover:bg-white/20 rounded-lg p-2 transition-colors"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>

            <div className="p-6 space-y-4">
              {/* Original Review */}
              <div className="bg-slate-50 rounded-xl p-4">
                <div className="flex items-center gap-2 mb-2">
                  <span className="font-medium text-slate-900">{selectedReview.author}</span>
                  {renderStars(selectedReview.rating)}
                </div>
                <p className="text-sm text-slate-700">{selectedReview.content}</p>
              </div>

              {/* Reply Input */}
              <div>
                <label className="block text-sm font-medium text-slate-700 mb-2">Your Response</label>
                <textarea
                  value={replyText}
                  onChange={(e) => setReplyText(e.target.value)}
                  rows={5}
                  placeholder="Write a thoughtful response to this review..."
                  className="w-full px-4 py-3 border border-slate-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-amber-500 focus:border-transparent resize-none"
                />
                <p className="text-xs text-slate-500 mt-1">This response will be posted publicly on {selectedReview.platform}.</p>
              </div>

              {/* AI Suggestions */}
              <div className="bg-blue-50 rounded-xl p-4">
                <p className="text-sm font-medium text-blue-700 mb-2 flex items-center gap-2">
                  <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M11.3 1.046A1 1 0 0112 2v5h4a1 1 0 01.82 1.573l-7 10A1 1 0 018 18v-5H4a1 1 0 01-.82-1.573l7-10a1 1 0 011.12-.38z" clipRule="evenodd" />
                  </svg>
                  AI Suggested Response
                </p>
                <p className="text-sm text-blue-800">
                  Thank you for taking the time to share your experience, {selectedReview.author}! We truly value your feedback and are thrilled to hear about your positive experience with our team.
                </p>
                <button
                  onClick={() => setReplyText(`Thank you for taking the time to share your experience, ${selectedReview.author}! We truly value your feedback and are thrilled to hear about your positive experience with our team.`)}
                  className="mt-2 text-sm text-blue-600 hover:text-blue-800 font-medium"
                >
                  Use this response
                </button>
              </div>

              {/* Actions */}
              <div className="flex gap-3 pt-4">
                <button
                  onClick={() => {
                    setShowReplyModal(false)
                    setSelectedReview(null)
                    setReplyText('')
                  }}
                  className="flex-1 px-4 py-3 border border-slate-300 text-slate-700 rounded-xl hover:bg-slate-50 font-medium"
                >
                  Cancel
                </button>
                <button
                  onClick={handleReplySubmit}
                  disabled={!replyText.trim()}
                  className="flex-1 px-4 py-3 bg-gradient-to-r from-amber-500 to-orange-500 text-white rounded-xl font-medium hover:from-amber-600 hover:to-orange-600 transition-all shadow-lg disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  Post Reply
                </button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Request Review Modal */}
      {showRequestModal && (
        <div className="fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-2xl max-w-md w-full shadow-2xl overflow-hidden">
            <div className="bg-gradient-to-r from-amber-500 to-orange-500 px-6 py-4 flex items-center justify-between">
              <div className="flex items-center gap-3">
                <div className="w-10 h-10 bg-white/20 rounded-xl flex items-center justify-center backdrop-blur-sm">
                  <svg className="w-5 h-5 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 10h.01M12 10h.01M16 10h.01M9 16H5a2 2 0 01-2-2V6a2 2 0 012-2h14a2 2 0 012 2v8a2 2 0 01-2 2h-5l-5 5v-5z" />
                  </svg>
                </div>
                <div>
                  <h3 className="text-lg font-semibold text-white">Request Review</h3>
                  <p className="text-sm text-amber-100">Send a review request to a customer</p>
                </div>
              </div>
              <button
                onClick={() => {
                  setShowRequestModal(false)
                  setRequestEmail('')
                  setRequestName('')
                  setRequestPhone('')
                }}
                className="text-white hover:bg-white/20 rounded-lg p-2 transition-colors"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>

            <div className="p-6 space-y-4">
              <div>
                <label className="block text-sm font-medium text-slate-700 mb-2">Customer Name *</label>
                <input
                  type="text"
                  value={requestName}
                  onChange={(e) => setRequestName(e.target.value)}
                  placeholder="Enter customer name"
                  className="w-full px-4 py-3 border border-slate-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-amber-500 focus:border-transparent"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-slate-700 mb-2">Email Address *</label>
                <input
                  type="email"
                  value={requestEmail}
                  onChange={(e) => setRequestEmail(e.target.value)}
                  placeholder="customer@example.com"
                  className="w-full px-4 py-3 border border-slate-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-amber-500 focus:border-transparent"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-slate-700 mb-2">Phone (Optional)</label>
                <input
                  type="tel"
                  value={requestPhone}
                  onChange={(e) => setRequestPhone(e.target.value)}
                  placeholder="+1 555-0123"
                  className="w-full px-4 py-3 border border-slate-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-amber-500 focus:border-transparent"
                />
              </div>

              <div className="bg-slate-50 rounded-xl p-4">
                <p className="text-sm text-slate-600">
                  A personalized email will be sent asking for a review. The customer can choose which platform to leave their review on.
                </p>
              </div>

              <div className="flex gap-3 pt-4">
                <button
                  onClick={() => {
                    setShowRequestModal(false)
                    setRequestEmail('')
                    setRequestName('')
                    setRequestPhone('')
                  }}
                  className="flex-1 px-4 py-3 border border-slate-300 text-slate-700 rounded-xl hover:bg-slate-50 font-medium"
                >
                  Cancel
                </button>
                <button
                  onClick={handleSendRequest}
                  disabled={!requestEmail.trim() || !requestName.trim()}
                  className="flex-1 px-4 py-3 bg-gradient-to-r from-amber-500 to-orange-500 text-white rounded-xl font-medium hover:from-amber-600 hover:to-orange-600 transition-all shadow-lg disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  Send Request
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
